define('benchmark-poker/controllers/comment', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    isEditing: false,

    actions: {
      edit: function edit() {
        this.set("isEditing", true);
      },

      save: function save() {
        this.set("isEditing", false);
        this.get("model").save();
      }
    }
  });

});