/* jshint ignore:start */

define('benchmark-poker/config/environment', ['ember'], function(Ember) {
  var prefix = 'benchmark-poker';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("benchmark-poker/tests/test-helper");
} else {
  require("benchmark-poker/app")["default"].create({"name":"benchmark-poker","version":"0.0.0.7eee5f6c"});
}

/* jshint ignore:end */
